
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&display=swap');

.home-main {
    background-color: #4158d0;
    background-image: linear-gradient(
      43deg,
      #4158d0 0%,
      #c850c0 46%,
      #ffcc70 100%
    );
    padding: 140px 0px 106px 0px;
    height: 100vh;
  }
  
  
  .homeWrapper {
    /* border: 1px solid red; */
    width: 75%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .homeLeft {
      /* border: 1px solid red; */
     
    width: 80%;
    display: flex;
    flex-direction: column;
    gap: 20px;
    justify-content: space-between;
  }
  
  .homeLeft > div:nth-child(1) {
      /* border: 1px solid red; */
    width: 100%;
    font-size: 70px;
    font-style: normal;
    font-weight: 400;
  
    
  }
  .homeLeft > div:nth-child(2) {
      /* border: 1px solid rgb(11, 166, 171); */
  padding-left: 6px;
  
    
  }
  
  .homeLeft a {
    width: 24%;
  }
  
  .home-resume {
      /* border: 1px solid yellow; */
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    gap:10px;
    font-weight: 600;
    cursor: pointer;
    width: 120px;
    margin-left: 0.5rem !important;
    border-radius: 5px !important;
    padding: 10px 20px !important;
    font-size: 1.2rem !important;
    letter-spacing: 1px !important;
    color: #e8e8e8 !important;
    background-color: #00899f;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 3px 10px;
    border: none !important;
  
  }
  
  .home-resume:hover {
    background: rgb(32, 149, 226)
;
    -moz-transition: all 0.2s ease-in-out;
    -o-transition: all 0.2s ease-in-out;
    -webkit-transition: all 0.2s ease-in-out;
    transition: all 0.2s ease-in-out;
    color: white;
  }
  
  .homeright {
    width: 500px;
  }
  
  .home-img {
    width: 50%;
    display: block;
    margin: auto;
  }
  
  /* meidum screens */
  @media all and (min-width: 768px) and (max-width: 1024px) {
    
    .homeLeft > div:nth-child(1) {
      font-size: 40px;
    }
    .home-img {
      width: 60%;
      display: block;
      margin: auto;
    }
    .homeright {
      width: 300px;
      /* height: 300px; */
    }
  
    .home-resume {
      width: 130px;
    }
  
    .homeLeft a {
      width: min-content;
    }
  }
  
  /* small screens */
  @media all and (min-width: 280px) and (max-width: 767px) {
    .home-main {
      padding: 60px 0px 0px 0px;
      height: 100%;
    }
    #user-detail-name{
      font-size: 14px;
    }
    #tagline p{
      font-size: 18px;
      color: white;
      margin-left: 10px;
      width:350px;
    }
  
    .homeWrapper {
       
      display: flex;
      flex-direction: column-reverse;
      justify-content: center;
      width: 100%;
    }
  
    .homeLeft {
      justify-content: center;
      width: 100%;
      align-items: center;
      padding: 0px;
      text-align: center;
    }
  
    .homeright {
      width: 300px;
    }
  
    .homeLeft > div:nth-child(1) {
      font-size: 40px;
    }
  
    .home-resume {
      width: 130px;
      margin-bottom: 20px;
    }
  
    .homeLeft a {
      width: min-content;
    }
    .right-section{
      width: 80%;
    }
    .right-section img{
      width: 100%;
    }
  }