
*{
  font-family: 'Poppins',sans-serif;
}
.logo{
  background-image: url(../../../public/logor.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 90px;
  height: 80px;
}
.navbar {
    width: 100%;
    background-color: #1e1e1e;
    transition: all 0.3s ease-out 0s !important;
    position: fixed;
    font-size: 18px;
    height: 60px;
    padding: 5px 0;
    z-index: 999;
    
  }
  
  .wrapper {
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    align-content: center;


  }
  
  .wrapperLeft img {  
    border-radius: 8px;
    margin-left: 40px;
    width: 70px;
    cursor: pointer;
  }
  /* .wrapperLeft img:hover {
    -moz-transition: all 0.1s ease-in-out;
    -o-transition: all 0.1s ease-in-out;
    -webkit-transition: all 0.1s ease-in-out;
    transition: all 0.4s ease-in-out;
  } */
  
  .wrapperRight {
    margin-right: 40px;
    display: flex;
    font-weight: bold;
    justify-content: space-around;
    align-items: center;
    width: 60%;

  }
  .wrapperRight > div {
    border-radius: 5px;
    transition: all 0.1s ease-in-out;
    padding: 6px 10px;
    font-weight: bold;
    color: white;
    font-family: 'monospace';
  }
  .wrapperRight > :nth-child(7) {
    border-radius: 5px;
    transition: all 0.1s ease-in-out;
    padding: 0 10px;
    
   
  }
  
  /* .wrapperRight > div:hover {
    color: #191919 ;
  } */
  .navbar-resume {
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    gap:10px;
    font-weight: 600;
    cursor: pointer;
    width: 120px;
    margin-left: 2rem !important;
    border-radius: 5px !important;
    padding: 10px 20px !important;
    font-size: 1.2rem !important;
    letter-spacing: 1px !important;
    color: #e8e8e8 !important;
    /* background-image: linear-gradient(43deg, #4158d0 0%, #c850c0 100%); */
    background: #00899f;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 3px 10px;
    border: none !important;
  }
  
  .navbar-resume:hover {
    transition: all 0.3s ease-in-out;
  }
  
  .wrapperRight a:hover {
    transition: all 0.3s ease-in-out;
    cursor: pointer;
  }
  
  .responce {
    display: none;
  }
  
  /*               medium size               */
  @media all and (min-width: 768px) and (max-width: 1024px) {
    .responce {
      margin-right: 40px;
      display: none;
      font-size: 30px;
    }
  
    .drawerbody {
      font-size: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
    }
  
    .drawerbody a:hover {
      cursor: pointer;
      color: #6a6a6a;
    }
  
    .navbar-resume {
      background-image: linear-gradient(43deg, #4158d0 0%, #c850c0 100%);
      color: white;
      padding: 15px;
    }
  
    .navbar-resume:hover {
      color: white;
      padding: 8px;
    }
  }
  @media all and (min-width: 280px) and (max-width: 767px) {
    .wrapperRight {
      display: none;
    }
  
    .responce {
      margin-right: 40px;
      display: block;
      font-size: 30px;
    }
  
    .drawerbody {
      font-size: 20px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
    }
  
    .drawerbody a:hover {
      cursor: pointer;
      color: #6a6a6a;
    }
  
    .navbar-resume {
      padding: 10px;
      background-color: #1a1a1a;
      color: white;
    }
    .navbar-resume:hover {
      color: white;
      padding: 8px;
      background: linear-gradient(to right, #000000, #434343);
    }
  }